// @mixin
// RTL
//
// src/scss/mixins/cross-browser/_rtl.scss
// -----------------------------------------------------------------------------

// /**
//  * rtl():
//  * Apply content when RTL mode.
//  * ```scss
//  * .component {
//  *    margin-left: 1em;
//  *
//  *  @include rtl() {
//  *    margin-left: 0;
//  *    margin-right: 1em;
//  *  }
//  * }
//  * ```
//  */

@mixin rtl() {
  html[dir=rtl] & {
    @content;
  }
}
